@mixin radial-background($from, $to) {
    background: radial-gradient(circle, $from 50%, $to 150%);
}

body {
    #root {
        #app {
            #content {
                .menu {
                    position: sticky;
                    top: 100px;

                    .menu-label {
                        color: var(--white1);
                    }
                    .menu-list {
                        a {
                            color: var(--link-blue);
                        }
                    }
                }

                #docs {
                    background-color: var(--black);
                    .content {
                        color: var(--white3);

                        h1 {
                            color: var(--white1);
                        }
                        h2,
                        h3,
                        h4 {
                            color: var(--white2);
                        }
                    }
                }
            }
        }
    }
}