body #root #app {
    .navbar {
        background-color: #000000aa;

        .navbar-brand {
            background-color: #000000aa;
        }

        .navbar-menu {
            background-color: #000000aa;
            a.navbar-item:focus, 
            a.navbar-item:focus-within, 
            a.navbar-item:hover, 
            a.navbar-item.is-active, 
            .navbar-link:focus, 
            .navbar-link:focus-within, 
            .navbar-link:hover, 
            .navbar-link.is-active { 
                background-color: #000000aa;
            }
        }

        .navbar-burger {
            height: 74px;
            color: var(--white1);
        }

        .navbar-item {
            text-align: center;
            color: white;

            .logo {
                max-height: 100% !important;
            }

            &.logo-text {
                padding-left: 5px;
                font-family: Audiowide;
                font-size: 28px;
                cursor: pointer;
            }
        }
    }
}
