@mixin transition-opacity($delay, $delayed: false) {
    transition: opacity $delay;

    @if $delayed {
        transition-delay: $delay;
    }
}

@mixin radial-background($from, $to) {
    background: radial-gradient(circle, $from 50%, $to 150%);
}

body {
    #root {
        #app {
            #content {
                #farm {
                    @include radial-background(var(--brown), var(--black));

                    .decoration {
                        display: flex;
                        justify-content: center;
                    }

                    .main {
                        padding: 1rem;
                        z-index: 1;
                        @include transition-opacity(250ms, true);

                        .balance-info {
                            .action {
                                display: flex;
                                justify-content: center;
                            }

                            .column-info {
                                margin-top: 1rem;
                                display: inline-flex;
                                font-family: "Nunito", sans-serif;
                                font-weight: 300;
                                color: var(--white);
                                text-transform: uppercase;
                                justify-content: center;

                                .info-value {
                                    display: -webkit-box !important;
                                }

                                @media screen and (max-width: 768px) {
                                    .column.is-full-mobile {
                                        justify-content: center !important;
                                        margin-top: -1em;
                                    }
                                }

                                .info-action {
                                    display: flex;
                                    justify-content: flex-end;
                                }

                                h2 {
                                    font-size: 1rem;
                                    text-align: center;

                                    &.info {
                                        font-size: 1.25rem;
                                        font-weight: 300;
                                    }

                                    &.multiplier {
                                        margin-left: 10px;
                                        margin-top: 2px;
                                        font-size: 1rem;
                                        font-weight: 200;
                                        text-transform: initial;
                                    }

                                    &.value {
                                        margin-left: 10px;
                                        font-size: 1.25rem;
                                        font-weight: 600;
                                        // font-family: "Grandstander", cursive;
                                        text-transform: initial;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
