@mixin transition-opacity($delay, $delayed: false) {
    transition: opacity $delay;

    @if $delayed {
        transition-delay: $delay;
    }
}

@mixin background($from, $to) {
    background: url(../assets/field.png) bottom no-repeat, radial-gradient(circle, $from 50%, $to 150%);
}

body {
    #root {
        #app {
            #content {
                #landing:before {
                    content: "";
                    position: fixed;
                    left: 0;
                    right: 0;
                    height: 100%;
                    -webkit-filter: blur(3px);
                    -moz-filter: blur(3px);
                    -o-filter: blur(3px);
                    -ms-filter: blur(3px);
                    filter: blur(3px);
                    @include background(var(--blue), var(--white));
                }

                #landing {
                    .main {
                        filter: blur(0) !important;
                        -webkit-filter: blur(0) !important;

                        .container {
                            span {
                                font-family: "Nunito", sans-serif;
                                font-weight: 300;
                                text-transform: uppercase;

                                h1 {
                                    font-size: 3.75rem;
                                    text-align: center;
                                    color: var(--white);
                                }

                                h2 {
                                    text-align: center;
                                    color: var(--white);

                                    &.subTitle {
                                        font-size: 1.75rem;
                                        font-weight: 300;
                                    }
                                }
                            }

                            #field {
                                display: block;
                                margin: auto;
                                display: inline-block;
                                position: relative;
                                width: 100%;
                                overflow: hidden;
                                padding: 2em;
                            }
                        }
                    }
                }
            }
        }
    }
}
